import { css } from 'glamor'

const container = css({
  width: '10em',
  height: '10em',
  backgroundSize: 'contain',
  margin: '1em',
  backgroundColor: 'transparent',
  border: '0',
  boxShadow: '0px 0px 30px -10px black',
})

export default {
  container,
}



